* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

body {
	background: linear-gradient( 90deg, rgb(143, 57, 255) 0%, rgb(239, 129, 253) 125%);
}

.todo-app {
	display: flex;
	flex-direction: column;
	justify-content: start;
	width: 520px;
	min-height: 600px;
	background: #131f4d;
	text-align: center;
	margin: 150px auto;
	border-radius: 10px;
	padding-bottom: 32px;
}

h1 {
	margin: 32px 0;
	color: rgb(255, 255, 255);
	font-size: 24px;
}

.complete {
	text-decoration: line-through;
	opacity: 0.2;
}

.todo-form {
	margin-bottom: 32px;
}

.todo-input {
	padding: 14px 32px 14px 16px;
	border-radius: 8px 0 0 8px;
	border: 2px solid #5d0cff;
	outline: none;
	width: 320px;
	background: transparent;
	color: #fff;
}

.todo-input::placeholder {
	color: #ffffff;
}

.todo-button {
	padding: 16px;
	border: none;
	border-radius: 0 8px 8px 0;
	cursor: pointer;
	outline: none;
	background: linear-gradient( 90deg, rgba(93, 12, 255, 1) 0%, rgba(155, 0, 250, 1) 100%);
	color: #fff;
	text-transform: capitalize;
}

.todo-input.edit {
	border: 2px solid #f36eff;
}

.todo-button.edit {
	background: linear-gradient( 90deg, #f36eff 0%, #f191fa 100%);
	padding: 16px 22px;
}

.todo-container {
	display: flex;
	flex-direction: row;
	position: relative;
}

.todo-row {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 4px auto;
	color: #fff;
	background: linear-gradient( 90deg, rgb(49, 224, 116) 0%, rgb(52, 185, 63) 100%);
	padding: 16px;
	border-radius: 5px;
	width: 90%;
}

.todo-row:nth-child(4n + 1) {
	background: linear-gradient( 90deg, rgba(93, 12, 255, 1) 0%, rgba(155, 0, 250, 1) 100%);
}

.todo-row:nth-child(4n + 2) {
	background: linear-gradient( 90deg, rgba(255, 12, 241, 1) 0%, rgba(250, 0, 135, 1) 100%);
}

.todo-row:nth-child(4n + 3) {
	background: linear-gradient( 90deg, rgba(20, 159, 255, 1) 0%, rgba(17, 122, 255, 1) 100%);
}

.icons {
	display: flex;
	align-items: center;
	font-size: 24px;
	cursor: pointer;
}

.delete-icon {
	margin-right: 7px;
	color: #fff;
}

.edit-icon {
	color: #fff;
}